.nav {
    display: flex;
    justify-content: center;
    margin-top: 25px;
}

.nav-item {
    color: #fff4e6;
    list-style: none;
    margin: 0 10px;
    font-size: 20px;
    cursor: pointer;
}

.nav-item:hover {
    color: #854442;
}

.nav-active {
    color: #854442;
}

.nav-active:hover{
    color: #fff4e6;
}
